import React, { useState, useEffect } from 'react';
import {
  Container,
  Typography,
  Paper,
  Box,
  CircularProgress,
  Alert,
  Button,
  TextField,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Grid,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  IconButton,
  Divider,
  Card,
  CardContent,
  CardActions,
} from '@mui/material';
import { Delete as DeleteIcon, Edit as EditIcon, AutoAwesome as AutoAwesomeIcon } from '@mui/icons-material';
import { brainTeasers as brainTeaserService } from '../services/api';
import Checkbox from '@mui/material/Checkbox';
import ListItemText from '@mui/material/ListItemText';

interface BrainTeaser {
  id: string;
  title: string;
  description: string;
  difficulty: 'easy' | 'medium' | 'hard';
  solution: string;
  publishDate: string;
  imageUrl?: string;
}

const DIFFICULTY_OPTIONS = [
  { value: 'easy', label: 'Easy - Challenging logic puzzles that require careful analysis' },
  { value: 'medium', label: 'Medium - Complex problems that test multiple skills' },
  { value: 'hard', label: 'Hard - Master-level challenges that push your limits' },
];
const DIFFICULTY_VALUES = DIFFICULTY_OPTIONS.map(opt => opt.value);

const Admin: React.FC = () => {
  const [teasers, setTeasers] = useState<BrainTeaser[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [editingTeaser, setEditingTeaser] = useState<BrainTeaser | null>(null);
  const [formData, setFormData] = useState({
    title: '',
    description: '',
    difficulty: 'medium' as 'easy' | 'medium' | 'hard',
    solution: '',
    imageUrl: '',
    publishDate: new Date().toISOString().split('T')[0],
  });
  const [success, setSuccess] = useState(false);
  const [difficultyMode, setDifficultyMode] = useState<'static' | 'random'>('static');
  const [selectedDifficulties, setSelectedDifficulties] = useState<string[]>(['intermediate']);
  const [isGenerating, setIsGenerating] = useState(false);
  const [generationPrompt, setGenerationPrompt] = useState('');
  const [generatedTeaser, setGeneratedTeaser] = useState<Partial<BrainTeaser> | null>(null);

  useEffect(() => {
    fetchBrainTeasers();
  }, []);

  const fetchBrainTeasers = async () => {
    setLoading(true);
    setError('');
    try {
      const response = await brainTeaserService.getAllTeasers();
      setTeasers(response.data);
    } catch (err: any) {
      setError(err.response?.data?.message || 'Error fetching brain teasers');
    } finally {
      setLoading(false);
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    try {
      const payload = {
        ...formData,
        publishDate: formData.publishDate,
      };
      if (editingTeaser) {
        await brainTeaserService.updateTeaser(editingTeaser.id, payload);
      } else {
        await brainTeaserService.createTeaser(payload);
      }
      await fetchBrainTeasers();
      resetForm();
    } catch (error: any) {
      setError(error.response?.data?.message || 'Error saving brain teaser');
    }
  };

  const handleEdit = (teaser: BrainTeaser) => {
    setEditingTeaser(teaser);
    setFormData({
      title: teaser.title,
      description: teaser.description,
      difficulty: teaser.difficulty,
      solution: teaser.solution,
      imageUrl: teaser.imageUrl || '',
      publishDate: teaser.publishDate,
    });
  };

  const handleDelete = async (id: string) => {
    if (window.confirm('Are you sure you want to delete this brain teaser?')) {
      try {
        await brainTeaserService.deleteTeaser(id);
        await fetchBrainTeasers();
      } catch (error: any) {
        setError(error.response?.data?.message || 'Error deleting brain teaser');
      }
    }
  };

  const resetForm = () => {
    setEditingTeaser(null);
    setFormData({
      title: '',
      description: '',
      difficulty: 'medium',
      solution: '',
      imageUrl: '',
      publishDate: new Date().toISOString().split('T')[0],
    });
  };

  const handleInputChange = (field: keyof typeof formData, value: string) => {
    setFormData({
      ...formData,
      [field]: value,
    });
  };

  const handleDifficultyChange = (event: any) => {
    const value = event.target.value;
    setSelectedDifficulties(typeof value === 'string' ? value.split(',') : value);
  };

  const handleGenerateTeaser = async () => {
    setIsGenerating(true);
    setError('');
    try {
      const response = await brainTeaserService.generateTeaser({
        prompt: generationPrompt,
        difficulty: formData.difficulty,
      });
      setGeneratedTeaser(response.data);
    } catch (err: any) {
      setError(err.response?.data?.message || 'Error generating brain teaser');
    } finally {
      setIsGenerating(false);
    }
  };

  const handleUseGeneratedTeaser = () => {
    if (generatedTeaser) {
      setFormData({
        ...formData,
        title: generatedTeaser.title || '',
        description: generatedTeaser.description || '',
        solution: generatedTeaser.solution || '',
      });
      setGeneratedTeaser(null);
      setGenerationPrompt('');
    }
  };

  if (loading) {
    return (
      <Box
        sx={{
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          minHeight: '60vh',
        }}
      >
        <CircularProgress />
      </Box>
    );
  }

  return (
    <Container maxWidth="lg" sx={{ mt: 4 }}>
      <Paper elevation={3} sx={{ p: 4 }}>
        <Typography variant="h4" gutterBottom>
          Manage Brain Teasers
        </Typography>

        {error && (
          <Alert severity="error" sx={{ mb: 2 }}>
            {error}
          </Alert>
        )}

        {/* AI Teaser Generator Section */}
        <Box sx={{ mb: 4 }}>
          <Typography variant="h5" gutterBottom sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
            <AutoAwesomeIcon /> AI Teaser Generator
          </Typography>
          <Card variant="outlined">
            <CardContent>
              <Grid container spacing={2}>
                <Grid item xs={12}>
                  <TextField
                    fullWidth
                    label="Generation Prompt"
                    multiline
                    rows={3}
                    value={generationPrompt}
                    onChange={(e) => setGenerationPrompt(e.target.value)}
                    placeholder="Describe the type of brain teaser you want to generate..."
                    helperText="Example: 'Create a logic puzzle about a treasure hunt with three clues'"
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <FormControl fullWidth>
                    <InputLabel>Difficulty</InputLabel>
                    <Select
                      value={formData.difficulty}
                      onChange={(e) => setFormData({ ...formData, difficulty: e.target.value as 'easy' | 'medium' | 'hard' })}
                      label="Difficulty"
                    >
                      {DIFFICULTY_OPTIONS.map(option => (
                        <MenuItem key={option.value} value={option.value}>
                          {option.label}
                        </MenuItem>
                      ))}
                    </Select>
                  </FormControl>
                </Grid>
              </Grid>
            </CardContent>
            <CardActions>
              <Button
                variant="contained"
                color="primary"
                onClick={handleGenerateTeaser}
                disabled={isGenerating || !generationPrompt}
                startIcon={isGenerating ? <CircularProgress size={20} /> : <AutoAwesomeIcon />}
              >
                {isGenerating ? 'Generating...' : 'Generate Teaser'}
              </Button>
            </CardActions>
          </Card>

          {generatedTeaser && (
            <Card variant="outlined" sx={{ mt: 2 }}>
              <CardContent>
                <Typography variant="h6" gutterBottom>
                  Generated Teaser
                </Typography>
                <Typography variant="subtitle1" gutterBottom>
                  {generatedTeaser.title}
                </Typography>
                <Typography variant="body1" paragraph>
                  {generatedTeaser.description}
                </Typography>
                <Typography variant="subtitle2" color="text.secondary">
                  Solution: {generatedTeaser.solution}
                </Typography>
              </CardContent>
              <CardActions>
                <Button
                  variant="contained"
                  color="primary"
                  onClick={handleUseGeneratedTeaser}
                >
                  Use This Teaser
                </Button>
                <Button
                  variant="outlined"
                  onClick={() => setGeneratedTeaser(null)}
                >
                  Discard
                </Button>
              </CardActions>
            </Card>
          )}
        </Box>

        <Divider sx={{ my: 4 }} />

        <Box component="form" onSubmit={handleSubmit} sx={{ mb: 4 }}>
          <Grid container spacing={2}>
            <Grid xs={12}>
              <TextField
                fullWidth
                label="Title"
                value={formData.title}
                onChange={(e) => handleInputChange('title', e.target.value)}
                required
              />
            </Grid>
            <Grid xs={12}>
              <TextField
                fullWidth
                label="Description"
                multiline
                rows={4}
                value={formData.description}
                onChange={(e) => handleInputChange('description', e.target.value)}
                required
              />
            </Grid>
            <Grid xs={12} sm={6}>
              <FormControl fullWidth margin="normal">
                <InputLabel>Difficulty Mode</InputLabel>
                <Select
                  value={difficultyMode}
                  onChange={e => setDifficultyMode(e.target.value as 'static' | 'random')}
                  label="Difficulty Mode"
                >
                  <MenuItem value="static">Static</MenuItem>
                  <MenuItem value="random">Random</MenuItem>
                </Select>
              </FormControl>
            </Grid>
            <Grid xs={12} sm={6}>
              <FormControl fullWidth margin="normal">
                <InputLabel>{difficultyMode === 'random' ? 'Difficulties' : 'Difficulty'}</InputLabel>
                <Select
                  multiple={difficultyMode === 'random'}
                  value={selectedDifficulties}
                  onChange={handleDifficultyChange}
                  label={difficultyMode === 'random' ? 'Difficulties' : 'Difficulty'}
                  renderValue={(selected) =>
                    Array.isArray(selected)
                      ? selected.map(val => DIFFICULTY_OPTIONS.find(opt => opt.value === val)?.label).join(', ')
                      : DIFFICULTY_OPTIONS.find(opt => opt.value === selected)?.label
                  }
                >
                  {DIFFICULTY_OPTIONS.map(option => (
                    <MenuItem key={option.value} value={option.value}>
                      {difficultyMode === 'random' && (
                        <Checkbox checked={selectedDifficulties.indexOf(option.value) > -1} />
                      )}
                      <ListItemText primary={option.label} />
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
            <Grid xs={12} sm={6}>
              <TextField
                fullWidth
                label="Image URL (optional)"
                value={formData.imageUrl}
                onChange={(e) => handleInputChange('imageUrl', e.target.value)}
              />
            </Grid>
            <Grid xs={12}>
              <TextField
                fullWidth
                label="Solution"
                multiline
                rows={2}
                value={formData.solution}
                onChange={(e) => handleInputChange('solution', e.target.value)}
                required
              />
            </Grid>
            <Grid xs={12}>
              <Box sx={{ display: 'flex', gap: 2 }}>
                <Button
                  type="submit"
                  variant="contained"
                  color="primary"
                  size="large"
                >
                  {editingTeaser ? 'Update' : 'Create'} Brain Teaser
                </Button>
                {editingTeaser && (
                  <Button
                    variant="outlined"
                    color="secondary"
                    onClick={resetForm}
                    size="large"
                  >
                    Cancel
                  </Button>
                )}
              </Box>
            </Grid>
          </Grid>
        </Box>

        <TableContainer>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>Date</TableCell>
                <TableCell>Title</TableCell>
                <TableCell>Difficulty</TableCell>
                <TableCell align="right">Actions</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {teasers.map((teaser) => (
                <TableRow key={teaser.id}>
                  <TableCell>{new Date(teaser.publishDate).toLocaleDateString()}</TableCell>
                  <TableCell>{teaser.title}</TableCell>
                  <TableCell>{teaser.difficulty}</TableCell>
                  <TableCell align="right">
                    <IconButton
                      color="primary"
                      onClick={() => handleEdit(teaser)}
                    >
                      <EditIcon />
                    </IconButton>
                    <IconButton
                      color="error"
                      onClick={() => handleDelete(teaser.id)}
                    >
                      <DeleteIcon />
                    </IconButton>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </Paper>
    </Container>
  );
};

export default Admin; 